		    	this.option.title.text = this.fullChartData.result[0].title	
	    		this.option.xAxis.min = this.fullChartData.result[0].xAxis	    	
		    	let legend = this.fullChartData.result[0].legend;
		    	let yAxis = this.fullChartData.result[0].yAxis;
		    	let seriesD = this.fullChartData.result[0].series;

		    	for(let i = 0; i<legend.length; i++ ){
		    		this.option.legend.data.push(legend[i])
		    		//console.log(legend[i])
		    	}

		    	for(let j = 0; j<yAxis.length; j++ ){
		    		this.option.yAxis.data.push(yAxis[j])
		    		//console.log(yAxis[j])
		    	}
		    	for(let g = 0; g<seriesD.length; g++ ){
		    		this.option.series[g].name = seriesD[g].name
		    		for(let k = 0; k<seriesD[g].data.length; k++){
		    			this.option.series[g].data.push(seriesD[g].data[k])  
		    			//console.log("wwwww"+JSON.stringify(seriesD[g].data[k]) )
		    		}

		    		//console.log("ttt"+series[g].data )
		    	}


