<!-- 资讯页内的政策项组件 -->
<template>
	<div id="Policy">
		<!-- <v-banner></v-banner> -->
    	<!-- 这里放新闻列表的组件 -->
    	<!-- <v-newslist></v-newslist> -->
    	<div class="banner-container">
	        <div class="swiper-container">
	            <div class="swiper-wrapper">

	                <div class="swiper-slide "
	                	> <!-- v-for="item in dataFlash" -->
	                    <img class="banner-img width100" src="../../assets/img/list-img.png"/>
	                    <div class="shade-backg colr-w  lucency text-cent">中国取消进口印度菜籽粕的禁令</div> 
	                </div> 
	            </div>
	             <div class="swiper-pagination"></div>
	        </div>
    	</div>
    	





    	<div class="bod-container">
	        <ul class="bod-warp">

	            <li class="bod-list clearfix" v-for="item in dataArr" >
	               
	                <div class="bod-list-info zixun">
	                    <p class="bod-list-title" @click="goDetail()">{{item.title}}</p>
	                    <ul class="source-wrap clearfix">
	                        <li class="float-lft source-text">来源：<span>{{item.src}}</span></li>
	                        <li class="float-rigt concern text-cent" @click="goFollow()">关注</li>
	                    </ul>
	                    <ul class="time-wrap clearfix">
	                        <li class="float-lft">{{item.pdate_src}}</li>
	                        <!-- <li class="float-rigt text-rigt message"><img class="message-ico" src="../assets/img/message.png" />412</li> -->
	                        <li class="float-rigt text-rigt check"><img class="check-ico"  src="../../assets/img/check.png"/>1108</li> 
	                    </ul>
	                </div>
	            </li>
 
	        </ul>
    	</div>

	</div>





	</div>
</template>

<script type="text/javascript">
	//import NewsList from './xinwen/news-list.vue';/*导入新闻列表组件*/
	//import Banner from './xinwen/banner.vue';/*导入轮播组件*/
import Swiper from 'swiper' 
import 'swiper/dist/css/swiper.min.css';


	const dataSrc = JSON.parse(sessionStorage.dataszx) //数据源
	console.log(dataSrc)
	export default {
		name:'Policy',
		data(){
			return {
				msg:'这是资讯页内的政策选项页',
				dataArr:null,
			}
		},
		components:{
			/*'v-banner':Banner,
			'v-newslist':NewsList*/
		},
		methods:{
			lunbo:function(){
				 var mySwiper = new Swiper ('.swiper-container', {
			        loop: true, // 循环模式选
			        pagination: {
			          el: '.swiper-pagination',
			        },
			        autoplay: {
				       /* delay: 3000,*/
				        disableOnInteraction: false,
				     },
		        }); 
			}
			
		},
		mounted(){
			this.dataArr = dataSrc.result;
			this.lunbo(); 
			
			


		}
	}
</script>
<style scoped>
	.bod-list .bod-list-info,.zixun{
		width: 100%;
	}



	.swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction{
        bottom: 32px;
    }

    .swiper-pagination-bullet-active{
        background:#fff; 
    }
</style>


